export default {
  namespaced: true,
  state: {
    items: [
      {
        url: '/a/orders',
        text: ''
      },
      {
        url: '/a/products/',
        text: '',
        children: [
          {
            url: '/a/products/cat',
            text: '',
          },
          {
            url: '/a/products/products-list',
            text: ' ',
          },
          {
            url: '/a/products/properties',
            text: '',
          }
        ]
      },
      {
        url: '/a/settings/',
        text: '',
        children: [
          {
            url: '/a/settings/order-reg',
            text: ' ',
          },
          {
            url: '/a/settings/delivery',
            text: '',
          },
          {
            url: '/a/settings/payments',
            text: '',
          },
          {
            url: '/a/settings/general',
            text: '',
          }
        ]
      },
    ],
    activeNavItemIndex: 0
  },

  getters: {
    items(state){
      return state.items;
    },
    activeNavItemChildren(state){
      let item = state.items[state.activeNavItemIndex];
      return item.children ? item.children : [];
    }
  },

  mutations: {
    setActiveNavItem (state, index) {
      state.activeNavItemIndex = index;
    }
  },
  actions: {
    setActiveNavItem(store, index){
      store.commit('setActiveNavItem', index);
    }
  }
}
